'use strict';

(function (factory) {

    "use strict";

    if (typeof define === 'function' && define['amd']) {
        define(['knockout', 'knockout-mapping', 'text!./view.html', 'app/data-context-blog', 'lib/auth', 'lib/url', 'lib/alerts', 'lib/knockout-bindings-ext', 'lib/knockout-bindings-datetime', 'lib/knockout-bindings-richText'], factory);
    }
})(function (ko, mapping, template, ctx, auth, url, alerts) {

    "use strict";

    function ViewModel(params) {

        var self = this;

        self.loaded = ko.observable(false);
        self.model = ko.observable();
        self.notFound = ko.observable();
        self.updateRunning = ko.observable(false);
        self.updateListingRunning = ko.observable(false);

        if (!auth.authenticated()) {
            auth.loginRedirect();
        }

        ctx.blogs.getById(params.id).done(function (data) {

            if (data) {
                self.model(new ArticleModel(data));
            } else {
                self.notFound(true);
            }

            self.loaded(true);
        });

        self.submit = function (form) {

            self.updateRunning(true);

            // Todo: fix this timeout issue
            setTimeout(function () {
                var _arguments = arguments;


                var args = mapping.toJS(self.model());

                ctx.blogs.update(args).then(function () {

                    url.load(url.resolve('#blog/', args.id));

                    alerts.info('Article successfully saved');
                }, function () {

                    console.log('Error occurred', _arguments);

                    alerts.info('There was an saving the article');
                }).then(function () {

                    self.updateRunning(false);
                });
            }, 200);
        };

        self.toggleShowInListings = function () {

            var model = self.model(),
                showInListings = !model.showInResults();

            self.updateListingRunning(true);

            ctx.blogs.updateListed(model.id(), showInListings).then(function () {

                model.showInResults(showInListings);
                alerts.success("This is article is now " + (showInListings ? 'visible' : 'hidden') + ' in listings. You do not need to save the article', 'Success');
            }, function (err) {

                console.log(err);
            }).then(function () {

                self.updateListingRunning(false);
            });
        };
    }

    function ArticleModel(data) {

        mapping.fromJS(data, {}, this);

        this.cancelLink = data.viewLink;
        this.newLabel = ko.observable(null);
    }

    ArticleModel.prototype.addLabel = function () {

        var newValue = this.newLabel();

        if (!!newValue) {
            this.labels.push({ id: null, value: newValue.toLowerCase() });
            this.newLabel(null);
        }
    };

    ArticleModel.prototype.removeLabel = function (data) {

        this.labels.remove(data);
    };

    return {
        viewModel: ViewModel,
        template: template
    };
});